


// Less
$gray-1: #ffffff;
$gray-2: #fafafa;
$gray-3: #f5f5f5;
$gray-4: #f0f0f0;
$gray-5: #d9d9d9;
$gray-6: #bfbfbf;
$gray-7: #8c8c8c;
$gray-8: #595959;
$gray-9: #434343;
$gray-10: #262626;
$gray-11: #1f1f1f;
$gray-12: #141414;
$gray-13: #000000;

$blue-6: blue;

$nnColor-1: #e6ffee;
$nnColor-2: #aaf2c4;
$nnColor-3: #7ce6a6;
$nnColor-4: #52d98d;
$nnColor-5: #2dcc77;
$nnColor-6: #0bbe64;
$nnColor-7: #029952;
$nnColor-8: #007341;
$nnColor-9: #004d2e;
$nnColor-10: #002618;


html {
	height: 100%;
	width: 100%;
	font-family: -apple-system,BlinkMacSystemFont,'Segoe UI',Roboto,'Helvetica Neue',Arial,'Noto Sans',sans-serif,'Apple Color Emoji','Segoe UI Emoji','Segoe UI Symbol','Noto Color Emoji';
    font-size: 14px;
}

html, ul.fancytree-container {
	font-family: -apple-system,BlinkMacSystemFont,'Segoe UI',Roboto,'Helvetica Neue',Arial,'Noto Sans',sans-serif,'Apple Color Emoji','Segoe UI Emoji','Segoe UI Symbol','Noto Color Emoji';
    font-size: 14px;
}

body {
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: column;
	margin: 0;
    padding: 0;
    overflow-x: hidden;
}

#n3-app {
	display: block;
	height: 100%;
	width: 100%;
}

#nn-top-menu {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
}

.n3-tree .n3-node-add,
.n3-layout_sidebar, .n3-tree ul.fancytree-container, .n3-filter, .n3-search .input,
.n3-tree,
.nn-nodes-list,
#nn-top-menu, #nn-top-menu ul,
#nn-note {
	background-color: $gray-2;
}

#nn-trash {
	background-color: $gray-5;
}

#nn-footer {
	position: fixed;
    bottom: 0;
    left: 0;
    background-color: $blue-6;
    right: 0;
    height: 20px;
    z-index: 100;
	padding: 0 9px;
	white-space: nowrap;
}

#nn-note {
	padding: 5px;
	display: flex;
	flex-direction: column;
	height: 100%;
}

#nn-footer, #nn-footer a {
	color: white;
}

.nn-spin-full-screen,
.nn-spin-full-screen .ant-spin-container {
	display: block;
	height: 100%;
	width: 100%;
	padding-bottom: 10px;
}

.nn-center-screen {
	display: flex;
	justify-content: center;
	text-align: center;
	min-height: 100vh;
	flex-direction: column;
}

.nn-flex-break {
	flex-basis: 100%;
	height: 0;
}

.n3-bar-vertical {
	display: flex;
	flex-direction: column;
	height: 100%;
}

.n3-bar-grow {
	flex-grow: 1;
}

.nn-site-tag-plus {
	background: #fff;
	border-style: dashed;
}
.nn-edit-tag {
	user-select: none;
}
.nn-tag-input {
	width: 78px;
	margin-right: 8px;
	vertical-align: top;
}


.nn-nodes-list {
	padding: 5px;
	overflow: auto;
	height: 100%;
}



.nn-search-breadCrumb,
.nn-breadCrumb-collapsed,
.nn-list-breadCrumb {
	color: $gray-6;
}

.nn-search-path {
	font-size: 12px;
}

.nn-list-breadCrumb {
	font-weight: 400;
	font-size: 12px;
	overflow: hidden;
	white-space: nowrap;
}

.nn-note-metadata {
	color: #000;
	white-space: nowrap;
}

