.n3-layout {
	display: flex;
	min-height: 100vh;
}

.n3-layout_sidebar {
	flex-shrink: 0;
	height: 100vh;
	overflow: hidden;
	position: sticky;
	top: 0;
	display: flex;
    flex-direction: column;
	min-width: 200px;
	flex: auto
}

.n3-layout_content {
	display: block;
	flex-grow: 1;
	z-index: 4;
	min-width: 200px;
}




/* ************************************************* */



.n3-note-title-row {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 10px;
}

.n3-note-title-input, .n3-note-title-input .input, .n3-note-title-input .input input {
	width: 100%;
}

.n3-note-title-input .input input {
	border: 0;
	font-size: 3rem;
    line-height: 3rem;
	padding: 0;
}

.n3 .ui.menu .ui.button {
    margin-right: 0.5em;
}


.n3-node-header {
	display: flex;
	align-items: center;
}

.n3-breadcrumb {
	display: inline;
	margin-left: 5px;
}

.tabulator-cell .breadcrumb {
	font-size: 10px;
	font-weight: 400;
}


.ui-fancytree {
	height: 100%;
}

.modal {
    z-index: 120; /* splitter has 100*/
}

.n3-node-desciption {
	height: 400px;
	padding: 10px;
	overflow: auto;
}

.n3-task-details-modal > .modal-content {
    width: 900px;
}

.n3-task-details-modal > .modal-content > .box {
	width: 880px;
}


.n3-task-details-modal .n3-attributes input, .n3-task-details-modal .n3-attributes select {
	width: 100px;
	max-width: 100px;
}

.tabulator [data-internstatus="DONE"] .n3title {
	color: #ccc;
	text-decoration: line-through !important;
}

.tabulator [data-internstatus="TOBEDONE"] .n3title {
	color: #333;
}

.tabulator [data-priority="1"] .n3priority, [data-priority="1"] .n3title {
	font-weight: 100;
}
.tabulator [data-priority="2"] .n3priority, [data-priority="2"] .n3title {
	font-weight: 400;
}
.tabulator [data-priority="3"] .n3priority, [data-priority="3"] .n3title  {
	font-weight: 1000;
}
/* fix for tagify and bulma*/
.input.is-static:hover {
    border-color: transparent;
}

.n3-notification {
	background-color: transparent;
}

.n3-title-info {
	color: blue;
	font-size: 11px;
}


.n3-tabInfo {
	font-weight: 700;
	font-size: 10px;
}



.task-list .task {
	background-color: #f8f8f8;
	padding: 9px !important;
	margin: 3px 0 3px 0;
}

.tox .n3backlinks-sidebar {
	width: 300px;
	border-left: 5px solid #eee;
}
.tox .n3backlinks-sidebar ul {
	margin-left: 20px;
}

.tox .n3backlinks-sidebar a {
	color: #4183c4;
	cursor: pointer;
}



.n3-search .input {
	width: 100%;
}

.n3-filter {
    width: 100%;
	border-bottom: 2px solid #aFaFaF;
}

.nn-header {
	padding: 5px;
	background-color: $nnColor-10;
}

.n3-tree {
	overflow: auto;
	flex: 1;
	/* left place for context menu */
		padding-left: 15px;
}

.fancytree-node {
	white-space: nowrap;
}

table.fancytree-ext-table {
	/*background-color: #333;*/
	width: 100%;
}

span.fancytree-title {
	/*color:#eee;*/
}

.n3-tree ul.fancytree-container {
    border: none;
}


.n3-tree .n3-node-add {
	display: none;
	position: absolute;
	right: 19px;
}

.n3-tree .n3-mouseover .n3-node-add {
	display: inline-block;
}

.n3-tree .ui.compact.icon.button,
.n3-tree .fancytree-checkbox,
.n3-tree .fancytree-node {
	z-index: 20;
}

.n3-node-background {
	display: none;
	position: absolute;
	left: 0;
	right: 0;
	background: rgba(104, 104, 104, 0.1);
	margin-left: 0;
	margin-right: 0;
	height: 21px;
	z-index: 1;
}
.n3-tree .n3-mouseover .n3-node-background, .n3-tree .fancytree-active .n3-node-background {
	display: inline;
}

.ui.transparent {
	background: transparent;
}

.nn-trash-background-color {
	background-color: red;
}

.fancytree-edit-input, .fancytree-edit-input:focus  {
    background-color: transparent;
    border: none;
	outline: none;
	margin: 0;
	padding: 0;
}


.fancytree-node {
	/* to fire mouse over on node event*/
		padding-left: 50px;
		margin-left: -50px;
	/* to position context menu trigger relativ to it */
		position: relative;
}

/*  position context menu trigger relativ to node span */
.nn-fancytree-node-context-menu-trigger {
	position: absolute;
	left: 35px;
	top: 2px;
	cursor: pointer;
}


